.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

img{
  z-index: -1;
  display: flex;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: transparent;
  display: fixed;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

h1{
  width: 40vw;
  height: 10vh;
  font-size: xx-large;
}

.menu p{
  display: inline-block;
  border-radius: 30px;
  margin: 5px 10px;
  padding: 5px 10px;
}
.menu p:hover{
  background-color: black;
  color: white;
  border-radius: 30px;
  transition: 500ms;
}

.menu{
  display: inline;
}
